<template>
  <div>
    <div class="input-group mb-3 mx-auto" style="width: 250px;">
      <input v-model="query" class="form-control" @keyup.enter="searchVideo(query)" placeholder="Search" aria-label="Recipient's username" aria-describedby="button-addon2">
      <button class="btn btn-outline-secondary" @click="searchVideo(query)" type="button" id="button-addon2"><i class="fas fa-search"></i></button>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  name: 'SearchBar',
  data() {
    return {
      query: ''
    }
  },
  methods: {
    ...mapActions(['searchVideo']),
  }
}
</script>

