<template>
  <li class="list-group-item video-item" @click="onVideoClick(video)">
    <img class="me-3" :src="`${video.snippet.thumbnails.default.url}`" alt="video-thumbnail">
    <div class="media-body d-flex flex-column justify-content-center">
      <span>
      {{ video.snippet.title }}
      </span>
    </div>
  </li>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  name: 'VideoListItem',
  props: {
    video: Object,
  },
  methods: {
    ...mapActions(['onVideoClick',])
  },
}
</script>

<style scoped>
.video-item {
  display: flex;
  cursor: pointer;
}
.video-item:hover {
  background-color:rgb(230, 255, 247);
}
</style>